.App{
  font-family: 'Arial';
  color: #333333;
  padding: 0px 20px;
  @media all and (max-width: 480px){
    padding: 0px 10px;
  }
  a{
    display: inline-block;
    color: inherit;
  }
  p{
    margin-bottom: 0px;
  }
  img{
    max-width: 100%;
  }
  h2{
    font-size: 28px;
    font-weight: 100;
  }
  .notification{
    background: #47a3a1;
    color: #fff;
    padding: 10px;
    max-width: 360px;
    width: 100%;
    font-size: 18px;
    p{
      margin: 0px;
      font-weight: 100;
    }
    @media all and (max-width: 480px){
      font-size: 16px;
    }
  }
  .description{
    font-size: 18px;
    background: #f9f9f9;
    padding: 10px;
    line-height: 1.5;
    @media all and (max-width: 480px){
      font-size: 16px;
    }
    a{
      color: #47a3a1;
    }
    p{
      margin: 0px;
    }
  }
  .container{
    max-width: 1190px;
    padding: 0px 10px;
  }
  .grid{
    display: flex;
    flex-wrap: wrap;
  }
  .button_default{
     max-width: 180px;
     width: 100%;
     height: 40px;
     font-size: 16px;
     margin: 10px 0px;
     color: #fff;
     border: 1px solid #41aeae;
     background: #41aeae;
  }
  .list{
    background: #f9f9f9;
    padding: 10px;
  }
  .list-skills{
    list-style: none;
    padding-left: 30px;
    font-size: 20px;
    line-height: 1;
    @media all and (max-width: 480px){
      font-size: 16px;
      padding-left: 10px;
    }
    li{
      margin: 0px 0px 15px;
      position: relative;
      padding-left: 25px;
      &::before{
        content: '';
        display: inline-block;
        width: 8px;
        left: 0px;
        bottom: 0px;
        top:0px;
        position: absolute;
        margin: auto;
        height: 8px;
        background: #41aeae;
        border-radius: 90px;
      }
    }
  }
  .SingleBlock{
    width: 27em;
    padding: 0px 8px;
    margin: 8px auto;
    @media all and (max-width: 991px){
      width: 20em;
    }
    @media all and (max-width: 768px){
      width: 100%;
    }
    .SingleBlock__preview{
      position: relative;
      overflow: hidden;
      height: 270px;
      @media all and (max-width: 991px){
        height: auto;
      }
      img{
        object-fit: cover;
      }
      .SingleBlock__title{
        font-size: 20px;
        @media all and (max-width: 480px){
          font-size: 16px;
        }
      }
      .SingleBlock__description{
        position: absolute;
        z-index: 10;
        top: 0px;
        left: 0px;
        bottom: -500px;
        margin-left: 10px;
        color: #fff;
        right: 0px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        transition: 0.3s;
        @media all and (max-width: 768px){
          bottom: 0px;
        }
      }
      &:after{
        content: '';
        width: 100%;
        height: 100%;
        display: block;
        position: absolute;
        left: 0px;
        right: 0px;
        bottom: 0px;
        top: 0px;
        z-index: 1;
        background: rgba(0,0,0,0);
        transition: 0.3s;
        @media all and (max-width: 768px){
          background: rgba(0,0,0,0.8);
        }
      }
      &:hover:after{
        background: rgba(0,0,0,0.8);
        transition: 0.3s;
      }
      &:hover{
        .SingleBlock__description{
          bottom: 0px;
        }
      }
    }
  }
}